<template>
  <div>
    <div
      class="modal fade"
      tabindex="-1"
      :id="'deleteCar-' + carData.ID"
      :class="'deleteCar-' + carData.ID"
      :aria-labelledby="carData.ID"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">{{ carData.name }}</h5>
            <button
              class="btn-close"
              type="button"
              aria-label="close"
              data-bs-dismiss="modal"
            ></button>
          </div>
          <div class="modal-body eachCarDetails">
            <img
              :src="getImageUrl(carData.image)"
              class="rounded img-fluid d-block mx-auto mb-3"
              :alt="carData.name"
              :title="carData.name"
            />
            <h4 class="text-center mb-2">
              {{ carData.name }} {{ carData.modelYear }}
            </h4>
            <hr class="bg-info" />
            <div class="d-flex justify-content-between">
              <button
                class="btn btn-outline-warning"
                type="button"
                data-bs-dismiss="modal"
                aria-label="Close"
              >
                Cancel
              </button>
              <button
                class="btn btn-outline-danger"
                type="button"
                @click="deleteCar(carData)"
                :disabled="result.success || result.error"
              >
                Delete
              </button>
            </div>
            <div class="clearfix my-3">
              <div
                class="alert"
                :class="result.success ? 'alert-success' : 'alert-danger'"
                role="alert"
                v-if="result.message"
              >
                {{ result.message }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "DeleteCar",
  props: {
    carData: Object,
  },
  data() {
    return {
      result: {
        success: false,
        error: false,
        message: "",
      },
    };
  },
  methods: {
    getImageUrl(imgName) {
      let img = require.context("@/assets/carsImages/");
      return img("./" + imgName);
    },
    async deleteCar(obj) {
      try {
        let fd = new FormData();
        fd.append("name", obj.name);
        fd.append("id", obj.ID);
        fd.append("image", obj.image);

        let result = await axios.post(
          `http://localhost/ShopDashboard/Backend/api.php?action=delete`,
          fd
        );

        this.result.success = true;
        this.result.error = false;
        this.result.message = result.data.message;

        // Reload the page after successful deletion
        setTimeout(() => {
          window.location.reload();
        }, 1500);
      } catch (error) {
        this.result.success = false;
        this.result.error = true;
        this.result.message = error.response
          ? error.response.data.message
          : "An error occurred.";
      } finally {
        setTimeout(() => {
          this.hideCurrentModal("deleteCar-" + this.carData.ID);
        }, 1000);
      }
    },
    hideCurrentModal(id) {
      document.getElementsByTagName("body")[0].removeAttribute("class");
      document.getElementsByTagName("body")[0].removeAttribute("style");

      // remove modal-backdrop element:
      const elements = document.getElementsByClassName("modal-backdrop");
      while (elements.length > 0) {
        elements[0].remove();
      }

      // remove modal html:
      const currentModal = document.getElementsByClassName(id);
      while (currentModal.length > 0) {
        currentModal[0].remove();
      }
    },
  },
};
</script>

