.eachCarDetails {
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.eachCarDetails img {
  width: 350px;
  height: 220px;
}

.bw {
  overflow-wrap: break-word;
}
</style>
