<template>
  <div class="home container">
    <h1 class="text-center mb-4">Car Shop</h1>
    <div class="mb-3">
      <button class="btn btn-success float-end" @click="viewAddNewCar">
        Add New Car
      </button>
      <div class="clearfix"></div>
    </div>
    <div v-if="emptyCars">
      <div role="alert" class="alert alert-warning fade show">
        {{ emptyCars }}
      </div>
    </div>
    <div class="row justify-content-around">
      <div v-if="loading" class="text-center">
        <div class="spinner-border text-primary" role="status">
          <span class="visually-hidden">Loading...</span>
        </div>
      </div>
      <div
        v-else
        class="col-12 col-md-6 col-lg-4 each-car"
        v-for="(eachCar, i) in cars"
        :key="i"
      >
        <img
          :src="getImageUrl(eachCar.image)"
          class="rounded img-fluid d-block mx-auto mb-3"
          :alt="eachCar.name"
          :title="eachCar.name"
        />
        <h4 class="text-center mb-2">
          {{ eachCar.name }} {{ eachCar.modelYear }}
        </h4>
        <div>
          <div class="float-start">
            <button class="btn btn-outline-success">
              {{ formatPrice(eachCar.price) }}
            </button>
          </div>
          <div class="float-end">
            <button
              class="btn btn-outline-primary"
              type="button"
              data-bs-toggle="modal"
              :data-bs-target="'#ShowCarDetails' + eachCar.ID"
            >
              More Details
            </button>
            <ShowCarDetails :carData="eachCar" />
          </div>
        </div>
        <div class="clearfix"></div>
        <hr class="bg-success" />
        <div>
          <div class="float-start">
            <button
              class="btn btn-danger"
              type="button"
              data-bs-toggle="modal"
              :data-bs-target="'#deleteCar-' + eachCar.ID"
            >
              Delete
            </button>
            <DeleteCar :carData="eachCar" />
          </div>
          <div class="float-end">
            <button
              type="button"
              class="btn btn-success"
              @click="UpdatePage(eachCar)"
            >
              Update
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ShowCarDetails from "@/components/ShowCarDetails.vue";
import DeleteCar from "@/components/DeleteCar.vue";
import axios from "axios";
export default {
  name: "HomeView",
  components: {
    ShowCarDetails,
    DeleteCar,
  },
  data() {
    return {
      cars: [],
      emptyCars: "",
      loading: true,
    };
  },
  mounted() {
    this.carsData();
  },
  methods: {
    getImageUrl(imgName) {
      let img = require.context("@/assets/carsImages/");
      return img("./" + imgName);
    },
    formatPrice(num) {
      return new Intl.NumberFormat("en-US", {
        style: "currency",
        currency: "USD",
      }).format(num);
    },
    viewAddNewCar() {
      this.$router.push({ name: "AddNewCar" });
    },
    UpdatePage(carData) {
      console.log(carData); // debug
      localStorage.setItem("currentCar", JSON.stringify(carData));
      this.$router.push({ name: "UpdateCar" });
    },
    async carsData() {
      try {
        let result = await axios.get(
          `http://localhost/ShopDashboard/Backend/api.php?action=read`
        );
        const resultData = result.data;
        if (resultData.status === "200") {
          this.cars = resultData.cars;
        } else {
          this.emptyCars = resultData.message;
        }
      } catch (error) {
        console.error("Error fetching data:", error);
        this.emptyCars = "Error fetching data.";
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

<style scoped>
.each-car {
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease;
}

.each-car:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.each-car img {
  width: 350px;
  height: 220px;
  transition: transform 0.3s ease;
}

.each-car:hover img {
  transform: scale(1.1);
}
</style>
