<template>
  <div class="container">
    <button class="btn btn-success mt-5" @click="viewHomePage">
      Home Page
    </button>
    <h1>Add New Car</h1>
    <div></div>
    <div>
      <!-- add new car form -->
      <form action="#" method="POST" enctype="multipart/form-data">
        <!-- msgs -->
        <div class="row d-grid gap-2 w300 col-auto d-block mx-auto">
          <div class="alert alert-success" role="alert" v-if="resultSuccess">
            {{ resultSuccessMsg }}
          </div>
          <div class="alert alert-danger" role="alert" v-if="resultErr">
            {{ resultErrMsg }}
          </div>
        </div>
        <!-- name -->
        <div class="row g-3 align-items-center mb-3">
          <div class="col-auto d-block mx-auto">
            <div
              class="form-floating"
              :class="{ 'from-data-error': carNameErr }"
            >
              <input
                type="text"
                id="carShopName"
                placeholder="Car Name"
                @input="validateCarName($event)"
                @change="validateCarName($event)"
                class="form-control w300"
                v-model.trim="carName"
              />
              <label for="carShopName">Car Name</label>
              <span class="error-feedback" v-if="carNameErr">
                {{ carNameMsg }}
              </span>
            </div>
          </div>
        </div>
        <!-- price -->
        <div class="row g-3 align-items-center mb-3">
          <div class="col-auto d-block mx-auto">
            <div
              class="form-floating"
              :class="{ 'from-data-error': carPriceErr }"
            >
              <input
                type="text"
                id="carShopPrice"
                @input="validateCarPrice($event)"
                @change="validateCarPrice($event)"
                placeholder="Car Price"
                class="form-control w300"
                v-model.trim="carPrice"
              />
              <label for="carShopName">Car Price</label>
              <span class="error-feedback" v-if="carPriceErr">
                {{ carPriceMsg }}
              </span>
            </div>
          </div>
        </div>
        <!-- model Year -->
        <div class="row g-3 align-items-center mb-3">
          <div class="col-auto d-block mx-auto">
            <div
              class="form-floating"
              :class="{ 'from-data-error': carModelYearErr }"
            >
              <input
                type="text"
                id="carShopModelYear"
                placeholder="Car Model Year"
                @input="validateCarModel($event)"
                @change="validateCarModel($event)"
                class="form-control w300"
                v-model.trim="carModelYear"
              />
              <label for="carShopName">Car Model Year</label>
              <span class="error-feedback" v-if="carModelYearErr">
                {{ carModelYearErrMsg }}
              </span>
            </div>
          </div>
        </div>
        <!-- image -->
        <div class="row g-3 align-items-center mb-3">
          <div class="col-auto d-block mx-auto">
            <div
              class="form-floating"
              :class="{ 'from-data-error': carImageErr }"
              v-if="!UploadedImage"
            >
              <input
                type="file"
                id="carShopImage"
                @input="validateCarImage()"
                @change="validateCarImage()"
                placeholder="Car Image"
                class="form-control w300 customFileField"
                ref="carImage"
              />
              <label for="carShopName">Car Image</label>
              <span class="error-feedback" v-if="carImageErr">
                {{ carImageMsg }}
              </span>
            </div>
            <div v-else>
              <span>Uploaded Car Image</span><br />
              <img :src="UploadedImage" class="w150 rounded" /><br />
              <button
                class="btn btn-outline-danger my-2"
                @click="removeImage()"
              >
                Remove
              </button>
            </div>
          </div>
        </div>
        <!-- description -->
        <div class="row g-3 align-items-center mb-3">
          <div class="col-auto d-block mx-auto">
            <div
              class="form-floating"
              :class="{ 'from-data-error': carDescriptionErr }"
            >
              <textarea
                id="carShopDescription"
                placeholder="Car Description"
                class="form-control w300 h125"
                @keyup="validateCarDescription($event)"
                @change="validateCarDescription($event)"
                v-model.trim="carDescription"
              ></textarea>
              <label for="carShopName">Car Description</label>
              <span class="error-feedback" v-if="carDescriptionErr">
                {{ carDescriptionMsg }}
              </span>
            </div>
          </div>
        </div>
        <!-- button -->
        <div class="row d-grid gap-2 w300 col-auto d-block mx-auto">
          <button
            class="btn btn-outline-success mb-2"
            @click.prevent="addNewCar()"
          >
            Add New Car
          </button>
          <button
            type="reset"
            class="btn btn-outline-primary mb-3"
            @click="resetFormError()"
          >
            Reset
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "AddNewCar",
  data() {
    return {
      carName: "",
      carNameMsg: "",
      carPriceMsg: "",
      carPrice: "",
      carModelYearErrMsg: "",
      carModelYear: "",
      carImage: "",
      carImageMsg: "",
      carDescriptionMsg: "",
      carDescription: "",
      resultSuccessMsg: "",
      resultErrMsg: "",
      UploadedImage: "",
      carNameErr: false,
      carPriceErr: false,
      carModelYearErr: false,
      carImageErr: false,
      carDescriptionErr: false,
      resultErr: false,
      resultSuccess: false,
      isCarNameValid: false,
      isCarPriceValid: false,
      isCarModelYearValid: false,
      isCarImageValid: false,
      isCarDescriptionValid: false,
    };
  },
  methods: {
    viewHomePage() {
      this.$router.push({ name: "home" });
    },
    // validate car name
    validateCarName(e) {
      let val = e.target.value;
      this.validateCarNameInput(val);
      // debug
      // console.log(e);
      // console.log(val);
    },
    validateCarNameInput(val) {
      // v1 for validation
      if (val == "") {
        this.isCarNameValid = false;
        this.carNameErr = true;
        this.carNameMsg = "Please enter a car name";
      } else if (val != "" && !isNaN(val)) {
        this.isCarNameValid = false;
        this.carNameErr = true;
        this.carNameMsg = "Please enter a name for the car";
      } else if (val.length < 3) {
        this.isCarNameValid = false;
        this.carNameErr = true;
        this.carNameMsg = "The name must be at least 10 characters";
      } else {
        this.isCarNameValid = true;
        this.carNameErr = false;
        this.carNameMsg = "";
      }
    },
    validateCarPrice(e) {
      let val = e.target.value;
      this.validateCarPriceInput(val);
    },
    validateCarPriceInput(val) {
      // v1 for validation
      if (val == "") {
        this.isCarPriceValid = false;
        this.carPriceErr = true;
        this.carPriceMsg = "Please enter the car price value";
      } else if ((val != "" && val <= 0) || val == 0.0) {
        this.isCarPriceValid = false;
        this.carPriceErr = true;
        this.carPriceMsg = "Please enter a valid car price value";
      } else if (val != "" && isNaN(val)) {
        this.isCarPriceValid = false;
        this.carPriceErr = true;
        this.carPriceMsg = "The value must be a number";
      } else {
        this.isCarPriceValid = true;
        this.carPriceErr = false;
        this.carPriceMsg = "";
      }
    },
    validateCarModel(e) {
      let val = e.target.value;
      this.validateCarModelInput(val);
    },
    validateCarModelInput(val) {
      // v1 for validation
      if (val == "") {
        this.isCarModelYearValid = false;
        this.carModelYearErr = true;
        this.carModelYearErrMsg = "Please enter the Model Year. ex: 2006";
      } else if (/^[0-9]+$/i.test(val) === false) {
        this.isCarModelYearValid = false;
        this.carModelYearErr = true;
        this.carModelYearErrMsg = "enter a valid Model Year. ex: 2006";
      } else if (val.length < 4 || val.length > 4) {
        this.isCarModelYearValid = false;
        this.carModelYearErr = true;
        this.carModelYearErrMsg = "enter a valid car Model Year. ex: 2006";
      } else {
        this.isCarModelYearValid = true;
        this.carModelYearErr = false;
        this.carModelYearErrMsg = "";
      }
    },
    validateCarDescription(e) {
      let val = e.target.value;
      this.validateCarDescriptionInput(val);
    },
    validateCarDescriptionInput(val) {
      // v1 for validation
      if (val == "") {
        this.isCarDescriptionValid = false;
        this.carDescriptionErr = true;
        this.carDescriptionMsg = "Please enter a Description.";
      } else if (val != "" && !isNaN(val)) {
        this.isCarDescriptionValid = false;
        this.carDescriptionErr = true;
        this.carDescriptionMsg = "Enter a valid car description";
      } else if (val && val.length > 100) {
        this.isCarDescriptionValid = false;
        this.carDescriptionErr = true;
        this.carDescriptionMsg = "The Description must be less than 100 chars";
      } else {
        this.isCarDescriptionValid = true;
        this.carDescriptionErr = false;
        this.carDescriptionMsg = "";
      }
    },
    validateFileExtension(id) {
      let fileInput = document.getElementById(id);
      let filePath = fileInput.value;
      // allowing file type:
      let allowExtension = /(\.jpg|\.jpeg|\.gif|\.png)/;
      if (!allowExtension.exec(filePath)) {
        return false;
      } else {
        return true;
      }
    },
    createImage(file) {
      new Image();
      let reader = new FileReader();
      reader.onload = (e) => {
        this.UploadedImage = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    removeImage() {
      this.UploadedImage = "";
      setTimeout(() => {
        this.validateCarImage();
      }, 500);
    },
    validateCarImage() {
      if (this.$refs.carImage.files[0]) {
        if (this.validateFileExtension("carShopImage") == true) {
          this.carImageErr = false;
          this.isCarImageValid = true;
          this.carImageMsg = "";
          this.createImage(this.$refs.carImage.files[0]);
          this.carImage = this.$refs.carImage.files[0];
        } else {
          this.carImageErr = true;
          this.isCarImageValid = false;
          this.carImageMsg = "The Image has to be EX:(.PNG,.JPG,.GIF)";
        }
      } else {
        this.carImageErr = true;
        this.isCarImageValid = false;
        this.carImageMsg = "Please Select Car Image.";
      }
    },
    async addNewCar() {
      // if all fields are validated:
      if (
        this.isCarNameValid &&
        this.isCarPriceValid &&
        this.isCarModelYearValid &&
        this.isCarImageValid &&
        this.isCarDescriptionValid
      ) {
        this.resultSuccess = true;
        this.resultErr = false;
        this.resultErrMsg = "";
        // show the form data before send it to the backend:
        let fd = new FormData();
        fd.append("name", this.carName);
        fd.append("description", this.carDescription);
        fd.append("image", this.carImage);
        fd.append("price", this.carPrice);
        fd.append("modelYear", this.carModelYear);
        // print this data in console for debugging:
        // for (let pair of fd.entries()) {
        //   console.log(pair[0] + "," + pair[1]);
        // }
        let result = await axios.post(
          `http://localhost/ShopDashboard/Backend/api.php?action=insert`,
          fd
        );
        const resultData = result.data;
        if (result.status == 200) {
          // console.log("Validation completed"); // debug
          this.resultSuccessMsg = "Added Successfully";
          this.resultSuccess = true;
          this.resultErr = false;
          this.resultErrMsg = "";
          setTimeout(() => {
            this.$router.push({ name: "home" });
          }, 2000);
          if (result.status == 400) {
            this.resultErrMsg = resultData.message;
            this.resultErr = true;
            this.resultSuccess = false;
          }
        }
      } else {
        // console.log("Validation failed"); // debug
        this.resultErrMsg = "Try Again!";
        this.resultErr = true;
        this.resultSuccess = false;
        this.resultSuccessMsg = "";
        this.validateCarNameInput(this.carName);
        this.validateCarPriceInput(this.carPrice);
        this.validateCarModelInput(this.carModelYear);
        this.validateCarDescriptionInput(this.carDescription);
        this.validateCarImage();
      }
    },
    resetFormError() {
      this.carNameErr = false;
      this.carPriceErr = false;
      this.carModelYearErr = false;
      this.carImageErr = false;
      this.carDescriptionErr = false;
      this.resultErr = false;
      this.resultSuccess = false;
      this.carNameMsg = "";
      this.carPriceMsg = "";
      this.carModelYearErrMsg = "";
      this.carImageMsg = "";
      this.carDescriptionMsg = "";
      this.resultSuccessMsg = "";
      this.resultErrMsg = "";
    },
  },
};
</script>

<style>
.w300 {
  width: 300px !important;
}
.w150 {
  width: 150px !important;
}
.h125 {
  height: 125px !important;
}
.from-data-error {
  color: red;
}
.from-data-error input,
.from-data-error textarea {
  border-color: red;
}
.error-feedback {
  padding-left: 3px;
  font-size: 0.9rem;
}
.customFileField {
  padding-left: 24px !important;
  padding-top: 38px !important;
  padding-bottom: 10px !important;
  height: 75px !important;
}
</style>
