<template>
  <div>
    <div
      class="modal fade"
      tabindex="-1"
      :id="'ShowCarDetails' + carData.ID"
      :aria-labelledby="carData.ID"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">{{ carData.name }}</h5>
            <button
              class="btn-close"
              type="button"
              aria-label="close"
              data-bs-dismiss="modal"
            ></button>
          </div>
          <div class="modal-body eachCarDetails">
            <img
              :src="getImageUrl(carData.image)"
              class="rounded img-fluid d-block mx-auto mb-3"
              :alt="carData.name"
              :title="carData.name"
            />
            <h4 class="text-center mb-2">
              {{ carData.name }} {{ carData.modelYear }}
            </h4>
            <hr class="bg-info" />
            <p class="text-muted bw">Description: {{ carData.description }}</p>
            <div>
              <div class="float-start">
                <button class="btn btn-outline-success">
                  {{ formatPrice(carData.price) }}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ShowCarDetails",
  props: {
    carData: Object,
  },
  methods: {
    getImageUrl(imgName) {
      let img = require.context("@/assets/carsImages/");
      return img("./" + imgName);
    },
    formatPrice(num) {
      return new Intl.NumberFormat("en-US", {
        style: "currency",
        currency: "USD",
      }).format(num);
    },
  },
};
</script>

<style>
.eachCarDetails {
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.eachCarDetails img {
  width: 350px;
  height: 220px;
}

.bw {
  overflow-wrap: break-word;
}
</style>
